  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(359deg);
  }
}
.-static {
  position: static !important;
}
.-section {
  margin-top: 15px;
}
.fade {
  opacity: 0;
  animation: fadeIn 0.3s linear forwards;
}
.containeroverflow {
  width: 100%;
}
@media (max-width: 767px) {
  .containeroverflow {
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
  }
}
html {
  font-size: 16px;
}
body {
  font-family: 'Open Sans', sans-serif;
  font-size: 1rem;
  color: #666;
  background-color: #e9ebee;
}
a {
  color: #3a7fb8;
}
a:hover,
a:focus,
a:active {
  color: #2e6693;
}
h1,
h2,
h3,
h4 {
  font-family: 'Montserrat', sans-serif;
  font-weight: bold;
  color: #4b4f56;
}
h1 {
  font-size: 1.625rem;
  margin: 0 0 1.25rem 0;
}
h2 {
  font-size: 1.375rem;
  margin: 0 0 0.9375rem 0;
}
h3 {
  font-size: 1.125rem;
  margin: 0 0 0.9375rem 0;
}
h4 {
  font-size: 1rem;
  margin: 0 0 0.625rem 0;
}
ul {
  margin: 0;
  padding: 0;
}
li {
  list-style: none;
}
object {
  display: block;
}
@import url("https://fonts.googleapis.com/css?family=Montserrat:400,700|Open+Sans:400,700");
@font-face {
  font-family: 'pr-cabeleireiros';
  src: url("../Fonts/pr-cabeleireiros.eot?60256622");
