}
.box-site .title-section {
  text-align: right;
}
.btn-default {
  color: #fff;
  background-color: #3b5998;
  border-color: #29487d;
}
.btn-default:hover,
.btn-default:active,
.btn-default:focus,
.btn-default:active:focus {
  color: #fff;
  background-color: #2f477a;
  border-color: #29487d;
}
.btn-top {
  font-weight: bold;
  color: #fff;
  background-color: #2f477a;
  border-color: #29487d;
}
.btn-top:hover,
.btn-top:active,
.btn-top:focus,
.btn-top:active:focus {
  color: #fff;
  background-color: #263962;
  border-color: #29487d;
}
.form-group {
  margin-bottom: 1.25rem;
}
.navbar-primary {
  color: #fff;
  background-color: #3b5998;
  border-bottom: 1px solid #29487d;
}
.navbar-primary a {
  color: #fff;
}
.nav-primrary > li > a:hover,
.nav-primrary > li > a:focus {
  background: rgba(0,0,0,0.1);
}
._no-border {
  border: none !important;
}
._overflow-hidden {
